@font-face {
  font-family: "KyivType_Serif-Bold";
  src: url("./assets/fonts/KyivType_Serif/Dmitry Rastvortsev - KyivType Serif Bold3.otf")
    format("opentype");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "KyivType_Serif-Medium";
  src: url("./assets/fonts/KyivType_Serif/Dmitry Rastvortsev - KyivType Serif Medium.otf")
    format("opentype");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "KyivType_Titling-Bold";
  src: url("./assets/fonts/KyivType_Titling/Dmitry Rastvortsev - KyivType Titling Bold.otf")
    format("opentype");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: "KyivType_Titling-Regular";
  src: url("./assets/fonts/KyivType_Titling/Dmitry Rastvortsev - KyivType Titling Regular2.otf")
    format("opentype");
  font-weight: normal;
  font-style: normal;
}

.mainSection {
  background: url("./assets/img/back/BacMain.png") center 0 no-repeat;
  /* background-size: cover; */
}

@media (max-width: 1400px) {
  .mainSection {
    background: url("./assets/img/back/BacMainMin.png") center 0 no-repeat;
  }
}
@media (min-width: 1900px) {
  .mainSection {
    background-size: contain;
  }
}

.flex {
  /* Класс который облегчает написание display: flex */
  display: flex;
  align-items: center;
  justify-content: space-between;
}
a:hover {
  /*ссылка при наведении прозрачнее и подчеркнутая*/
  text-decoration: underline;
  filter: brightness(85%);
}
button:hover {
  /*делает кнопку прозрачнее при наведении*/
  filter: brightness(85%);
}
ul {
  /* Удаляем точки у всех списков */
  list-style: none;
}
